<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel('byCode')" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">公共安全视频区域摄像头分布</div>
                <div id="TopBar" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="12">
                <techo-card resizeEvent="leftCard" @onUpLevel="handleUpLevel('byCode24')" :maxCard="true" :upLevel="isShowLevelBtu24">
                    <div slot="header">各区域24小时值守摄像机数量</div>
                    <div id="LeftBar" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="12">
                <techo-card resizeEvent="rightCard" :maxCard="true">
                    <div slot="header">高清摄像头占比</div>
                    <div id="RightPie" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 公共安全视频区域摄像头分布
      topBarOptions: {},
      topBarChart: null,
      // 公共安全视频区域区级摄像头分布
      leftBarOptions: {},
      leftBarChart: null,
      // 高清摄像头占比
      rightPieOptions: {},
      rightPieChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      byCode24: "",
      isShowLevelBtu24: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    },
    byCode24(value) {
      this.isShowLevelBtu24 = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 公共安全视频区域摄像头分布
    this.topBarChart = this.$echarts.init(this.$el.querySelector("#TopBar"));
    // 公共安全视频区域区级摄像头分布
    this.leftBarChart = this.$echarts.init(this.$el.querySelector("#LeftBar"));
    // 高清摄像头占比
    this.rightPieChart = this.$echarts.init(
      this.$el.querySelector("#RightPie")
    );
    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getOrgData(val.data.rawdata.code);
        this.getOrgPieData(val.data.rawdata.code);
      }
    });
    this.leftBarChart.on("click", val => {
      if (this.byCode24.length != 14) {
        this.getLevelOrgData(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("leftCard", () => {
      that.leftBarChart.resize();
    });
    this.$bus.$on("rightCard", () => {
      that.rightPieChart.resize();
    });
  },
  created() {
    /** 公共安全视频区域摄像头分布 */
    this.getOrgData();
    /** 公共安全24小时值守摄像机数量 */
    this.getLevelOrgData();
    /** 高清摄像头占比 */
    this.getOrgPieData();
  },

  methods: {
    // 公共安全视频区域摄像头分布
    getOrgData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "CompreSafeMonitor",
        fieldName: "monitorCount"
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityAndFieldSum(param, data => {
        if (data.length == 0) return;
        this.byCode = data[0].code;
        this.topBarOptions.yAxis.name = "高清摄像头数";
        this.topBarOptions.legend.data = ["摄像头"];
        that.topBarOptions.series = [
          {
            name: "摄像头",
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[0]
              }
            },
            barMaxWidth: 80,
            data: []
          }
        ];
        _.map(data, item => {
          that.topBarOptions.xAxis.data.push(item.name);
          that.topBarOptions.series[0].data.push({
            rawdata: item,
            value: item.sum
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },
    // 公共安全24小时值守摄像机数量
    getLevelOrgData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "CompreSafeMonitor",
        fieldName: "guardMonitorCount"
      };
      let that = this;
      this.leftBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityAndFieldSum(param, data => {
        if (data.length == 0) return;
        this.byCode24 = data[0].code;
        this.leftBarOptions.yAxis.name = "高清摄像头数";
        this.leftBarOptions.legend.data = ["摄像头"];
        this.leftBarOptions.series = [
          {
            name: "摄像头",
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[0]
              }
            },
            barMaxWidth: 80,
            data: []
          }
        ];
        _.map(data, item => {
          that.leftBarOptions.xAxis.data.push(item.name);
          that.leftBarOptions.series[0].data.push({
            rawdata: item,
            value: item.sum
          });
        });
        this.leftBarChart.setOption(that.leftBarOptions);
        this.leftBarChart.resize();
      });
    },
    // 高清摄像头占比
    getOrgPieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "CompreSafeMonitor",
        fieldNames: "highdefMonitorCount,monitorCount"
      };
      let that = this;
      this.rightPieOptions = _.cloneDeep(pieOptions);
      service.requestByEntityFieldSum(param, data => {
        that.rightPieOptions.legend.data = ["高清摄像头", "其他"];
        that.rightPieOptions.title.text = "高清摄像头占比";
        that.rightPieOptions.series[0].name = "高清摄像头";
        that.rightPieOptions.series[0].data.push({
          name: "高清摄像头",
          value: _.find(data, item => {
            return item.code == "highdefMonitorCount";
          }).sum
        });
        that.rightPieOptions.series[0].data.push({
          name: "其他",
          value:
            _.find(data, item => {
              return item.code == "monitorCount";
            }).sum -
            _.find(data, item => {
              return item.code == "highdefMonitorCount";
            }).sum
        });

        that.rightPieChart.setOption(that.rightPieOptions);
        that.rightPieChart.resize();
      });
    },


    // getOrgPieData(disCode) {
    //   let param = {
    //     districtCode: disCode || this.DISCODE,
    //     entityName: "CompreSafeMonitor",
    //     fieldName: "highdefMonitorCount"
    //   };
    //   let that = this;
    //   this.rightPieOptions = _.cloneDeep(pieOptions);
    //   service.requestByFieldName(param, data => {
    //     that.rightPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
    //     that.rightPieOptions.title.text = " ";
    //     that.rightPieOptions.series[0].name = "高清摄像头";
    //     data = data.filter(item => item.count != 0);
    //     _.map(data, item => {
    //       that.rightPieOptions.series[0].data.push({
    //         name: item.name,
    //         value: item.count
    //       });
    //     });
    //     that.rightPieOptions.series[0].radius = "45%";
    //     that.rightPieOptions.series[0].center = ["65%", "50%"];
    //     that.rightPieChart.setOption(that.rightPieOptions);
    //     that.rightPieChart.resize();
    //   });
    // },
    /** 返回上一级 */
    handleUpLevel(value) {
      let districtCode = "";
      switch (this[value].length) {
        case 9:
          districtCode = this[value].slice(0, 4);
          break;
        case 12:
          districtCode = this[value].slice(0, 6);
          break;
        case 14:
          districtCode = this[value].slice(0, 9);
          break;
        default:
          break;
      }
      if (value == "byCode") {
        this.getOrgData(districtCode);
        this.getOrgPieData(districtCode);
      } else {
        this.getLevelOrgData(districtCode);
      }
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 20rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 21.2rem);
    width: 100%;
    .el-col-12 {
      height: 100%;
    }
  }
}
</style>
